/* --------         FONT TYPE         -------- */
@font-face {
    font-family: 'CMU Typewriter Text';
    src: local('CMU Typewriter Text'), url(./fonts/cmuntt.ttf) format('truetype');
}

@font-face {
    font-family: 'CMU Typewriter Text (Bold)';
    src: local('CMU Typewriter Text (Bold)'), url(./fonts/cmuntb.ttf) format('truetype');
}

/* --------        COMMON USAGE       -------- */
@mixin fullscreen {
    min-height: 100%;
    height: 100%;
    max-height: 100%;

    min-width: 100%;
    width: 100%;
    max-width: 100%;
}


$line-height-factor: 1.25;
$firefox--line-height-factor: 1.00;
  
/* --------     LIGHT MODE COLOURS    -------- */
  
$light-background: #fafafa;
$light-text-color:  #111111;
$light-good: #9acd32; 
$light-bad: #a80000;
$light-input: #d3d3d3;

/* --------     DARK MODE COLOURS     -------- */

$dark-background: #222222;
$dark-text-color:  #e0e0e0;
$dark-good: #466900; 
$dark-bad: #d87777;
$dark-input: #414141;